import newaddPopup from "@/components/popup/newadd-popup.vue"

import {mapState,mapGetters,mapActions,mapMutations} from "vuex"
export default {
	components:{
		newaddPopup
	},
	data() {
		return {
			sortValue:0
		}
	},
	props:{
		item:{
			type:Object,
			default:null
		}
	},
	computed:{
		...mapState({
			goodsTypeList:state=>state.goods.goodsTypeList
		}),
		...mapGetters([
	
		]),
	},
	methods: {
		...mapMutations([
			'updateGoodsTypeList'
		]),
		...mapActions([
		]),
		changeType(){
			this.$refs.addPopup.showPopup(this.item)
		},
		deleteType(){
			var _that = this;
			uni.showModal({
			    title: '提示',
			    content: '删除 '+_that.item.Name+' 类别',
			    success: function (res) {
			        if (res.confirm) {
			            _that.deleteTypeCallBack()
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		sortValueInput(e){
			this.sortValue = e.detail.value
			console.log(this.sortValue)
		},
		deleteTypeCallBack(){
			var _that = this;
			_that.$H.post('/api/ProductType/DelProductType',{
				ID:_that.item.ID
			},{
				token:true
			}).then(res=>{
				console.log(res)
				if(res.status === 0){
					let tempList = this.goodsTypeList
					for (var i = 0; i < tempList.length; i++) {
						if(tempList[i].ID === res.data){
							tempList.splice(i, 1)
							return
						}
					}
					this.updateGoodsTypeList(tempList)
				}else{
					uni.showToast({
						title:res.message,
						icon:'none',
						duration:1500
					})
				}
			})
		}
	}
}
